@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Figtree:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  font-family: 'Figtree', sans-serif;
  font-size: var(--body-font-size);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

h1, .heading-1 {
  font-size: var(--font-size-7xl);
}

h2, .heading-2 {
  font-size: var(--font-size-6xl);
}

h3, .heading-3 {
  font-size: var(--font-size-5xl);
}

h4, .heading-4 {
  font-size: var(--font-size-4xl);
}

h5, .heading-5 {
  font-size: var(--font-size-3xl);
}

h6, .heading-6 {
  font-size: var(--font-size-2xl);
}

p {
  font-size: var(--font-size-default);
  font-weight: var(--font-weight-medium);
  line-height: 1.4;
  margin-bottom: 20px;
}

small {
  font-size: var(--font-size-xs);
}

a {
  color: var(--color-primary);
}

.fineprint {
  color: $color-neutral-gray-400;
}

// Font sizes classes
@each $name, $value in $font-sizes {
  .font-size-#{$name} {
    font-size: var(--font-size-#{$name}) !important;
  }
}

// Font weights classes
@each $name, $value in $font-weights {
  .font-weight-#{$name} {
    font-weight: var(--font-weight-#{$name}) !important;
  }
}

// Text colors classes
@each $name, $value in $theme-colors {
  .text-#{$name} {
    color: var(--color-#{$name}) !important;
  }
}

.text-center {
  text-align: center !important
}

.text-right {
  text-align: right !important
}

.text-left {
  text-align: left !important
}

/* Additional Typography */
.h4-figtree-bold {
  color: var(--text-text-primary);
  font-size: var(--font-size-4xl);
  text-align: center;
  font-style: normal;
  font-weight: var(--font-weight-bold);
  line-height: 110%; /* 39.6px */
}

.paragraph-s-figtree-medium {
  color: var(--text-text-secondary);
  font-size: var(--font-size-default);
  font-style: normal;
  font-weight: var(--font-weight-normal);
  line-height: 125%; /* 17.5px */
}

.caption-figtree-medium {
  color: var(--text-text-tertiary);
  font-size: var(--font-size-sm);
  text-align: center;
  font-style: normal;
  font-weight: var(--font-weight-medium);
  line-height: 17.5px; /* 133.333% */
}

.error {
  color: var(--error-color-error-primary);
  font-size: var(--font-size-sm);
  font-style: normal;
  font-weight: var(--font-weight-normal);
  line-height: 125%; /* 17.5px */
}

.caption-frn {
  color: var(--brand-color-brand-primary);
  text-align: center;
  font-size: var(--font-size-sm);
  font-style: normal;
  font-weight: 600;
  line-height: 16px; /* 133.333% */
  text-decoration-line: underline;
}

.subtitle-figtree-bold {
  color: #000;
  text-align: center;
  font-size: var(--font-size-xl);
  font-style: normal;
  font-weight: var(--font-weight-bold);
  line-height: 28px; /* 140% */
}

.paragraph-m-figtree-medium {
  color: var(--text-text-tertiary);
  /* font-family: Figtree; */
  font-size: var(--font-size-default);
  font-style: normal;
  font-weight: var(--font-weight-medium);
  line-height: 130%; /* 20.8px */
}
