.Notification {
    .k-notification {
        background-color: #fff;
        border-color: #dfdfdf;
        color: #000;
        width: 360px;
        max-width: 100%;
        padding: 20px;
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    .k-notification-content {
        font-size: var(--font-size-default);
    }

    .k-notification-actions {
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .k-notification-status {
        background-color: var(--color-success);
        color: #fff;
        display: inline-block;
        width: 32px;
        height: 32px;
    }

    .k-icon {
        font-size: 20px;
    }
}
